/*********** envestor-area style **************/
.envestor-area {
  background: #fdfdfd;
}

.envestor-slider .item {
  margin: 20px 5px 5px 5px;
}
.envestor-slider .owl-nav {
  text-align: center;
  margin-top: 30px;
}
.envestor-slider .owl-nav button {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  color: #377dff;
  border: 0;
  background: #f7f7f7;
  color: #292929;
  transition: all 0.3s ease-in;
  cursor: pointer;
}
.envestor-slider .owl-nav button:hover {
  background: #377dff;
  color: #fff;
}
.envestor-slider .owl-nav .owl-prev {
  margin-right: 20px;
}

.single-envestor {
  box-shadow: 0px 0px 10px 0px rgba(204, 204, 204, 0.46);
  padding-bottom: 16px;
  transition: all 0.3s ease-in;
  position: relative;
  background: #fff;
}
.single-envestor:hover {
  box-shadow: 0px 25px 40px 0px rgba(204, 204, 204, 0.46);
}
.single-envestor:hover .social-area {
  opacity: 1;
}
.single-envestor .name {
  font-size: 18px;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  font-family: var(--heading-font);
  color: #292929;
  margin: 25px 0 6px;
}
.single-envestor p {
  font-family: "Poppins", sans-serif;
  font-family: var(--heading-font);
  font-size: 16px;
  color: #768495;
}
.single-envestor .social-area {
  position: absolute;
  bottom: 0;
  right: 50px;
  background: #fff;
  padding: 30px 37px;
  opacity: 0;
  transition: all 0.3s ease-in;
}
.single-envestor .social-area li a {
  font-size: 18px;
  color: #377dff;
  background: #f7f7f7;
}
.single-envestor .social-area li a:hover {
  background: #377dff;
  color: #fff;
}

.envestor-area-2 .envestor-slider .owl-nav button:hover,
.envestor-area-2 .single-envestor .social-area li a:hover {
  background: #ff536d;
}

/*********** partner-area style **************/
