/*********** sign-up-area style **************/
.sign-up-area {
  background: #fff;
  padding: 30px;
  margin-top: -70px;
  z-index: 1;
  position: relative;
}
.sign-up-area .media .media-left {
  border-radius: 50%;
  border: 1px solid #e6e6e6;
  height: 80px;
  width: 80px;
  text-align: center;
  line-height: 80px;
  margin-right: 20px;
}
.sign-up-area .media .media-left i {
  color: #377dff;
  font-size: 24px;
}
.sign-up-area .media .media-body h5 {
  font-size: 24px;
  font-weight: 600;
  color: #292929;
}
.sign-up-area form {
  width: 100%;
  position: relative;
}
.sign-up-area form:after {
  content: "";
  position: absolute;
  left: -30px;
  top: 50%;
  background: #eeeeee;
  height: 76px;
  width: 2px;
  margin-top: -38px;
}
.sign-up-area form .form-group {
  width: 100%;
  margin-bottom: 0;
}
.sign-up-area form .form-group .form-control {
  height: 80px;
  border: 1px solid #e6e6e6;
  border-radius: 50px;
  color: #c4c4c4;
  padding: 0 150px 0px 46px;
  background: transparent;
}
.sign-up-area form .form-group .form-control::-webkit-input-placeholder {
  color: #c4c4c4;
}
.sign-up-area form .form-group .form-control:-ms-input-placeholder {
  color: #c4c4c4;
}
.sign-up-area form .form-group .form-control::placeholder {
  color: #c4c4c4;
}
.sign-up-area form button {
  position: absolute;
  right: 0;
  top: 0;
  color: #377dff;
  font-weight: 500;
  font-size: 16px;
  height: 80px;
  background: #fff;
  border: 0;
  border-radius: 50px;
  padding: 0 45px;
  cursor: pointer;
  box-shadow: 0px 3px 73px 0px rgba(229, 228, 227, 0.68);
}

.sign-up-area.bg-pink .media .media-body h5 {
  color: #fff;
}
.sign-up-area.bg-pink .media .media-left {
  background: #fff;
}
.sign-up-area.bg-pink .media .media-left i {
  color: #ff536d;
}
.sign-up-area.bg-pink form button {
  color: #ff536d;
}
.sign-up-area.bg-pink form .form-group .form-control {
  color: #fff;
}
.sign-up-area.bg-pink form .form-group .form-control::-webkit-input-placeholder {
  color: #fff;
}
.sign-up-area.bg-pink form .form-group .form-control:-ms-input-placeholder {
  color: #fff;
}
.sign-up-area.bg-pink form .form-group .form-control::placeholder {
  color: #fff;
}

/*********** back-to-top style **************/
.back-to-top {
  display: block;
  position: fixed;
  bottom: 20px;
  cursor: pointer;
  right: 40px;
  background: #001064;
  height: 50px;
  width: 50px;
  line-height: 53px;
  text-align: center;
  border-radius: 50%;
  z-index: 9;
  transition: all 0.3s ease-in;
}
.back-to-top:hover {
  background: #377dff;
}
.back-to-top i {
  color: #fff;
  font-size: 24px;
}
.back-to-top.back-to-top-2 {
  background: #ff536d;
}

