}

.body-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9;
  content: "";
  left: 0;
  top: 0;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in;
  cursor: url(data:image/png;base64,), progress;
}

.body-overlay.active {
  visibility: visible;
  opacity: 0.8;
}

/*********** btn style **************/
