  left: 15%;
  top: 50px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  font-size: 18px;
  line-height: 70px;
  text-align: center;
  background-color: var(--main-color);
  color: #fff;
  transform: translateX(-50%);
